import { useState } from "react";
import { FileInfo } from "./lib";
import UploadPanel from "./UploadPanel";
import HarFileInfo from "./HarFileInfo";
import "./App.css";
import { Button } from "antd";

const App = () => {
  const [fileInfo, setFileInfo] = useState<FileInfo | null>(null);

  return <>
    <div className="headline">
      Upload a HAR file and you'll see the videos as links. Nothing is sent to the server.
    </div>
    <UploadPanel
      setFileInfo={setFileInfo}
    />
    {fileInfo &&
      <>
        <h2>Results</h2>
        <HarFileInfo
          fileInfo={fileInfo}
        />
        <Button onClick={() => {
          setFileInfo(null);
          document.location = document.location;
        }}>Clear</Button>
      </>
    }
  </>;
};

export default App;